.footer {
  margin-top: auto;
}

.header {
  background-color: var(--color-primary);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__branding svg {
  height: 85px;
}

.header__content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__content > * {
  margin-left: auto;
}

.header__search {
  display: none;
  margin-top: 24px;
}

@media (min-width: 960px) {
  .header__search {
    display: block;
    margin-top: 0;
  }
}

.header__menu-toggle {
  order: 1;
  margin-top: auto;
  margin-bottom: auto;
}

.header__menu-toggle > * {
  float: right;
}

@media (min-width: 960px) {
  .header__menu-toggle {
    display: none;
  }
}

.header__menu {
  display: none;
  margin-top: auto;
  margin-bottom: auto;
}

@media (min-width: 960px) {
  .header__menu {
    display: block;
  }
}

.header__off-canvas {
  position: absolute;
  top: 0;
  transition: 0.3s;
}

.header__off-canvas--is-off-canvas {
  width: 0;
  height: 0;
  transform: translateX(-150px);
}

.header__off-canvas--is-off-canvas > * {
  display: none;
}

.header__off-canvas--is-on-canvas {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  background-color: var(--color-white);
}

.header__off-canvas--is-on-canvas > * {
  display: block;
}

