    flex-wrap: nowrap;
    justify-content: space-between;
    text-align: left;
  }
  .housekeeping__content > * {
    margin-bottom: 2rem;
    width: calc((100% / 1) - 2rem);
    margin-right: 1rem;
    margin-left: 1rem;
  }
  .housekeeping__content > * {
    margin-bottom: 0;
  }
}

