 */
.layout-tabs {
  background: var(--color-black);
}

.layout-tabs ul.tabs {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding-left: 0;
}

.layout-tabs li {
  margin: 0;
  list-style: none;
}

.layout-tabs li a {
  display: inline-block;
  padding: 0.66rem 1rem;
  text-decoration: none;
  color: black;
  background-color: lightgrey;
  font-weight: bold;
}

.layout-tabs li a.is-active {
